/*gallery*/

.gallery-wrapper {
  position: relative;
  z-index: 1;
  background: darken($secondary-color, 12%);
  background: linear-gradient(to left, darken($secondary-color, 8%), darken($secondary-color, 12%));
  .content {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 3vh 0;

    .gallery {
      &-full {
        position: relative;
        display: flex;
        align-items: center;

        .swiper-container {
          .swiper-wrapper {
            display: flex;
            height: 80vh;
            align-items: center;

            .swiper-slide {
              position: relative;
              height: 50vh;
              transition: all .4s ease-out;
              box-sizing: border-box;
              width: auto;
              opacity: .25;

              &.swiper-slide-prev,
              &.swiper-slide-next {
                width: 20% !important
              }

              &.swiper-slide-active {
                height: 80vh;
                width: 60% !important;
                opacity: 1;

                &:hover {
                  .zoom {
                    opacity: 1;
                    pointer-events: auto;
                    visibility: visible
                  }
                }
              }

              .image {
                width: 100%;
                height: 100%;

                img {
                  width: 100%;
                  height: 100%;
                  display: block;
                  object-fit: cover
                }
              }

              .overlay {
                position: absolute;
                left: 0;
                bottom: 0;
                z-index: 2;
                width: 100%;
                height: auto;
                padding: 50px 50px 30px 50px;
                background: rgb(0, 0, 0);
                background: linear-gradient(0deg, rgba(0, 0, 0, .8) 0%, rgba(0, 0, 0, .4) 50%, rgba(0, 0, 0, 0) 100%);
                display: flex;
                align-items: center;
                opacity: 0;
                visibility: hidden;
                transition: all .3s ease-out;

                &.show {
                  opacity: 1;
                  visibility: visible;

                  .text-wrap {
                    opacity: 1;
                    visibility: visible;
                    transform: translateY(0)
                  }
                }

                .text-wrap {
                  display: flex;
                  flex-direction: column;
                  max-width: 600px;
                  opacity: 0;
                  visibility: hidden;
                  transform: translateY(50px);
                  transition: all .4s linear;

                  .name {
                    span {
                      font-size: 2.2em;
                      font-weight: 700;
                    }
                  }

                  .caption {
                    p {
                      font-size: 1.16em;
                      color: #fff;
                      line-height: 1.7;
                    }
                  }
                }
              }
            }
          }
        }

        .swiper-next-button,
        .swiper-prev-button {
          position: absolute;
          z-index: 99;
          outline: none;
          transition: all .2s linear;
          width: 20%;
          height: 50vh;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          cursor: pointer;

          &.swiper-button-disabled {
            opacity: .2
          }
        }

        .swiper-next-button {
          right: 0;
          padding-left: 5vw;
          justify-content: flex-start
        }

        .swiper-prev-button {
          left: 0;
          padding-right: 5vw;
          transform: rotate(180deg);
        }
      }

      &-thumb {
        position: relative;
        width: 100%;
        max-width: 1020px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        padding-left: 12px;

        .swiper-container {
          width: 100%;

          .swiper-wrapper {
            .swiper-slide {
              position: relative;
              height: 10vh;
              box-sizing: border-box;
              cursor: pointer;

              &.swiper-slide-thumb-active {
                .image {
                  box-shadow: inset 0 0 0 2px $bg;
                  padding: 2px;

                  .overlay {
                    opacity: 1
                  }
                }
              }

              .image {
                width: 100%;
                height: 100%;
                position: relative;

                img {
                  width: 100%;
                  height: 100%;
                  display: block;
                  object-fit: cover;
                }

                .overlay {
                  position: absolute;
                  left: 2px;
                  top: 2px;
                  background-color: rgba($secondary-color, .6);
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  color: #fff;
                  width: calc(100% - 4px);
                  height: calc(100% - 4px);
                  opacity: 0;
                  transition: all .2s linear
                }
              }
            }
          }
        }

        .swiper-next-button {
          right: -15px
        }

        .swiper-prev-button {
          left: -5px;
          transform: rotate(180deg);
        }

        .swiper-next-button,
        .swiper-prev-button {
          position: absolute;
          z-index: 99;
          outline: none;
          transition: all .2s linear;
          width: 32px;
          height: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;

          &.swiper-button-disabled {
            opacity: .2
          }
        }
      }
    }

    .swiper-next-button,
    .swiper-prev-button {
      svg {
        path, circle {
          will-change: fill, stroke;
          transition: stroke 0.3s linear, fill 0.3s linear;
          opacity: 1;
        }
      }

      @media (hover: hover) {
        &:hover {
          svg {
            path {
              stroke: $white;
            }

            circle {
              fill: $button;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1024px) {
  .gallery-wrapper {
    .content {
      padding: 5vh 0;

      .gallery {
        &-full {
          .swiper-container {
            .swiper-wrapper {
              height: 70vh;

              .swiper-slide {
                height: 50vh;

                &.swiper-slide-prev,
                &.swiper-slide-next {
                  width: 10% !important
                }

                &.swiper-slide-active {
                  height: 70vh;
                  width: 80% !important
                }

                .overlay {
                  padding: 20px;

                  .text-wrap {
                    transform: translateY(0);

                    .name {
                      span {
                        font-size: 1.6em
                      }
                    }

                    .caption {
                      p {
                        font-size: 1.06em
                      }
                    }
                  }
                }
              }
            }
          }

          .swiper-next-button,
          .swiper-prev-button {
            width: 10%;
            height: 50vh;
            align-items: center;
            justify-content: center;

          }

          .swiper-next-button {
            padding-left: 0
          }

          .swiper-prev-button {
            padding-right: 0
          }
        }

        &-thumb {
          padding: 0 30px;
          max-width: 100%;

          .swiper-container {
            .swiper-wrapper {
              .swiper-slide {
                height: 80px
              }
            }
          }

          .swiper-next-button {
            right: 10px
          }

          .swiper-prev-button {
            left: 10px
          }
        }
      }
    }
  }

}