* {
  box-sizing: border-box;
}

html {
  overflow-y: -moz-scrollbars-vertical;
  overflow-y: scroll;
}

body, html {
  position: relative;
  background-color: #16161A;
  width: 100%;
  font-family: "Cinzel", serif;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  line-height: 1.75;
  overflow-x: hidden;
  image-rendering: -webkit-optimize-contrast;
}

main {
  align-items: flex-start;
  flex: 1 0 auto;
  justify-content: flex-start;
  max-width: 2560px;
  z-index: 2;
  margin: 0 auto;
  width: 100%;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
