  padding: 10px;
  display: flex;
  justify-content: center;
  color: #fff;
  box-shadow: 0 3px 5px rgba(22, 22, 26, 0.9);
  background-color: rgba(22, 22, 26, 0.9);
}
.header__nav {
  width: 100%;
  max-width: 1780px;
  padding-right: 30px;
